import React from 'react'
import contact from '../styles/contactform.module.scss'

class ContactForm extends React.Component {

  render() {
    const formStyle = {
      marginTop: `1rem`,
      width: `40rem`,
    }
    return (
      <form style={formStyle} className="form" action={this.props.action} method="post">
        <div>
          <input type="text" name="fullname" placeholder = "Name" className= {contact.feedbackinput} />
        </div>
        <div>
          <input type="email" name="email"  placeholder = "Email"  className= {contact.feedbackinput}/>
        </div>
        <div>
          <textarea style={{resize: `none`}} name="message" placeholder = "Message" className= {contact.feedbackinput}></textarea>
        </div>
              <button type="submit"  className="button special">SUBMIT</button>
      </form>
    );
  }
}

export default ContactForm